___CSS_LOADER_EXPORT___.push([module.id, `@charset "UTF-8";
:root {
  --height-triangle: 50px;
  --width-triangle: 50vw;
  --color-green: #00b35c;
}

.main {
  position: absolute;
  height: 100%;
  width: 100%;
}

.header {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  height: 35%;
  background: linear-gradient(to right, #004daa, var(--color-green));

  nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    top: 0;
    margin-top: 15px;
    padding: 0 10px;
    width: 100%;
    height: 10%;
    font-size: 1.2em;
  }

  /* Estilo do botão de ajuda */
  nav > a:nth-child(1) {
    display: flex;
    align-items: center;
    text-decoration: none;
  }

  nav > a:nth-child(1) .help {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 25px;
    background-color: white;
    width: 30px;
    height: 30px;
    border-radius: 50%;
  }

  nav > a:nth-child(1) .help span {
    color: black;
    font-weight: bold;
  }

  /* Estilo para quando o usuário não está logado */
  nav > a[href="/login"] {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    border-radius: 15px;
    text-decoration: none;
    font-size: 20px;
    padding: 5px 15px;
    transition: background-color 0.3s ease, transform 0.3s ease;
  }

  nav > a[href="/login"]:hover {
    background-color: lightgray;
    transform: scale(1.05);
  }

  nav > a[href="/login"] > span:nth-child(1) {
    margin-right: 10px;
    color: black;
    font-weight: bold;
  }

  nav > a[href="/login"] > span:nth-child(2) {
    font-size: 1.6em;
    color: var(--color-green);
    font-variation-settings: 'FILL' 1;
  }

  /* Estilo para quando o usuário está logado */
  nav > div {
    display: flex;
    align-items: center;
    gap: 15px; /* Espaçamento entre "Perfil" e "Deslogar" */
  }

  nav > div > a {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    border-radius: 15px;
    text-decoration: none;
    font-size: 20px;
    padding: 5px 10px;
    transition: background-color 0.3s ease, transform 0.3s ease;
  }

  nav > div > a:hover {
    background-color: lightgray;
    transform: scale(1.05);
  }

  nav > div > a > span:nth-child(1) {
    margin-left: 10px;
    color: black;
    font-weight: bold;
  }

  nav > div > a > span:nth-child(2) {
    font-size: 1.6em;
    margin-right: 10px;
    color: var(--color-green);
    font-variation-settings: 'FILL' 1;
  }



  .triangle-right-home, .triangle-left-home  {
    position: absolute;
    bottom: 0;
    height: 0;
    width: 0;
  }

  .triangle-right-home {
    left: 0;
    border-right: var(--width-triangle) solid transparent;
    border-left: 0 solid transparent;
    border-bottom: var(--height-triangle) solid white;
  }

  .triangle-left-home {
    right: 0;
    border-right: 0 solid transparent;
    border-left: var(--width-triangle) solid transparent;
    border-bottom: var(--height-triangle) solid white;
  }

  .tittle {
    display: flex;
    position: absolute;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top: 20%;
    width: 100%;
    height: 50%;
    font-weight: bold;
    background-color: rgba(255, 255, 255, 0.4);
    color: white;

    h1 {
      font-size: 5em;
    }

    h2 {
      font-size: 1.6em;
    }
  }
}

.container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  margin-top: 10%;

  > .row {
    display: flex;
    gap: 10px;
    justify-content: center;
  
    > div {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      font-size: 1.2em;
      width: 30vw;
    }
  
    > div > button {
      padding: 10px;
      margin: 5px;
      border-radius: 20px;
      background-color: transparent;
      border: 5px solid var(--color-green);
    }

    > div > span {
      font-weight: bolder;
    }
  
    > div > button > a > span {
      font-size: 4em;
      color: var(--color-green);
      font-weight: bolder;
    }
  }
}`, "",{"version":3,"sources":["webpack://./frontend/assets/css/home.css"],"names":[],"mappings":"AAAA,gBAAgB;AAChB;EACE,uBAAuB;EACvB,sBAAsB;EACtB,sBAAsB;AACxB;;AAEA;EACE,kBAAkB;EAClB,YAAY;EACZ,WAAW;AACb;;AAEA;EACE,aAAa;EACb,kBAAkB;EAClB,uBAAuB;EACvB,mBAAmB;EACnB,WAAW;EACX,kEAAkE;;EAElE;IACE,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,kBAAkB;IAClB,MAAM;IACN,gBAAgB;IAChB,eAAe;IACf,WAAW;IACX,WAAW;IACX,gBAAgB;EAClB;;EAEA,6BAA6B;EAC7B;IACE,aAAa;IACb,mBAAmB;IACnB,qBAAqB;EACvB;;EAEA;IACE,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,eAAe;IACf,uBAAuB;IACvB,WAAW;IACX,YAAY;IACZ,kBAAkB;EACpB;;EAEA;IACE,YAAY;IACZ,iBAAiB;EACnB;;EAEA,iDAAiD;EACjD;IACE,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,uBAAuB;IACvB,mBAAmB;IACnB,qBAAqB;IACrB,eAAe;IACf,iBAAiB;IACjB,2DAA2D;EAC7D;;EAEA;IACE,2BAA2B;IAC3B,sBAAsB;EACxB;;EAEA;IACE,kBAAkB;IAClB,YAAY;IACZ,iBAAiB;EACnB;;EAEA;IACE,gBAAgB;IAChB,yBAAyB;IACzB,iCAAiC;EACnC;;EAEA,6CAA6C;EAC7C;IACE,aAAa;IACb,mBAAmB;IACnB,SAAS,EAAE,4CAA4C;EACzD;;EAEA;IACE,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,uBAAuB;IACvB,mBAAmB;IACnB,qBAAqB;IACrB,eAAe;IACf,iBAAiB;IACjB,2DAA2D;EAC7D;;EAEA;IACE,2BAA2B;IAC3B,sBAAsB;EACxB;;EAEA;IACE,iBAAiB;IACjB,YAAY;IACZ,iBAAiB;EACnB;;EAEA;IACE,gBAAgB;IAChB,kBAAkB;IAClB,yBAAyB;IACzB,iCAAiC;EACnC;;;;EAIA;IACE,kBAAkB;IAClB,SAAS;IACT,SAAS;IACT,QAAQ;EACV;;EAEA;IACE,OAAO;IACP,qDAAqD;IACrD,gCAAgC;IAChC,iDAAiD;EACnD;;EAEA;IACE,QAAQ;IACR,iCAAiC;IACjC,oDAAoD;IACpD,iDAAiD;EACnD;;EAEA;IACE,aAAa;IACb,kBAAkB;IAClB,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,QAAQ;IACR,WAAW;IACX,WAAW;IACX,iBAAiB;IACjB,0CAA0C;IAC1C,YAAY;;IAEZ;MACE,cAAc;IAChB;;IAEA;MACE,gBAAgB;IAClB;EACF;AACF;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,mBAAmB;EACnB,eAAe;;EAEf;IACE,aAAa;IACb,SAAS;IACT,uBAAuB;;IAEvB;MACE,aAAa;MACb,sBAAsB;MACtB,mBAAmB;MACnB,kBAAkB;MAClB,gBAAgB;MAChB,WAAW;IACb;;IAEA;MACE,aAAa;MACb,WAAW;MACX,mBAAmB;MACnB,6BAA6B;MAC7B,oCAAoC;IACtC;;IAEA;MACE,mBAAmB;IACrB;;IAEA;MACE,cAAc;MACd,yBAAyB;MACzB,mBAAmB;IACrB;EACF;AACF","sourcesContent":["@charset \"UTF-8\";\r\n:root {\r\n  --height-triangle: 50px;\r\n  